import styled, { css } from "styled-components";

const MarginContainer = styled.div`
    ${({topS}) => topS && css`
    margin-top: var(--spacing-s);
    `}  

    ${({topBase}) => topBase && css`
        margin-top: var(--spacing-base);
    `}

    ${({topM}) => topM && css`
        margin-top: var(--spacing-m);
    `}   
`;

export default MarginContainer;